<template>
  <div>
    <div class="filter-container">
      <el-row :gutter="10">
        <el-col :sm="24" :md="12">
          <el-button class="filter-item" @click="handleCreate" type="primary" icon="el-icon-plus">{{$t('table.add')}}</el-button>
          <el-button class="filter-item" type="danger" :disabled="multipleSelection.length <= 0"
            v-if="multipleSelection.length>0"
            @click="handleDelete(multipleSelection)"
            icon="el-icon-delete">{{$t('table.delete')}} ({{multipleSelection.length}} item)</el-button>
        </el-col>
        <el-col :sm="24" :md="12">
          <el-input placeholder="Search" v-model="urlParams.s" class="input-with-select" @keyup.enter.native="getRoles">
            <el-button slot="append" icon="el-icon-search" @click="getRoles"></el-button>
          </el-input>
        </el-col>
      </el-row>
    </div>

    <div class="table-container">
      <el-table :data="roles" v-loading="loading" element-loading-text="Memuat data..." @selection-change="handleSelectionChange" border fit highlight-current-row style="width: 100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="name" label="Name" width="180" sortable>
        </el-table-column>
        <el-table-column label="Access">
          <template slot-scope="scope">
            <div v-for="access in scope.row.accesses" :key="access.access_id">
                <i class="el-icon-check"></i>
                <span style="margin-left: 10px">{{ access.menu }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column align="center" :label="$t('table.actions')" width="230" class-name="small-padding fixed-width">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="handleUpdate(scope.row)" icon="el-icon-edit">{{$t('table.edit')}}</el-button>
            <el-button type="danger" size="mini" style="width:80px;" @click="handleDelete([scope.row])" icon="el-icon-delete">{{$t('table.delete')}}</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- Pagination -->
      <div class="pagination">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pagination.currentPage"
          :page-sizes="pagination.sizes"
          :page-size="pagination.pageSize"
          layout="sizes, total, prev, pager, next"
          :total="pagination.total">
        </el-pagination>
      </div>
    </div>

    <el-dialog :title="textMap[formDialog.status]" :visible.sync="formDialog.visible" :width="formDialog.dialog.width">
      <el-form :model="roleForm" :rules="roleFormRules" ref="roleForm" :label-position="formDialog.dialog.labelPosition" label-width="100px">
        <el-form-item label="Name" prop="name">
          <el-input type="text" v-model="roleForm.name" placeholder="name"></el-input>
        </el-form-item>

        <el-form-item label="Access" prop="access_ids">
          <el-checkbox-group v-model="roleForm.access_ids" style="display:flex;flex-wrap:wrap">
            <div v-for="access in accesses" :key="access.access_id" style="width:150px;">
              <el-checkbox :label="access.access_id">{{access.menu}}</el-checkbox>
            </div>
          </el-checkbox-group>
        </el-form-item>

        <el-form-item>
          <el-button @click="formDialog.visible = false">{{$t('table.cancel')}}</el-button>
          <el-button v-if="formDialog.status==='create'" :loading="loading" type="primary" @click="createData">{{$t('table.confirm')}}</el-button>
          <el-button v-else type="primary" @click="updateData">{{$t('table.confirm')}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title="Delete Confirmation"
      :visible.sync="deleteDialog.visible"
      :width="formDialog.dialog.width"
      center>
      <span>Are you sure want to delete?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog.visible = false">Cancel</el-button>
        <el-button type="primary" @click="deleteData">Yes</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { dialog } from "@/responsive";

export default {
  data() {
    return {
      roles: [],
      accesses: [],
      loading: false,
      textMap: {
        update: "Edit",
        create: "Create"
      },

      // form
      roleForm: {
        name: "",
        access_ids: []
      },
      roleFormRules: {
        name: [{ required: true }],
        access_ids: [{ required: true }]
      },

      // Add, edit dialog
      formDialog: {
        visible: false,
        status: "",
        selectedId: undefined,
        dialog: dialog.md,
      },

      // delete dialog
      deleteDialog: {
        visible: false,
        dialog: dialog.md30,
      },
      deleteRoleIds: [],

      // table
      multipleSelection: [],

      // Pagination
      pagination: {
        total: 0,
        pageSize: 10,
        currentPage: 1,
        sizes: [5, 10, 20, 30, 40, 50, 100]
      },

      // Get
      urlParams: {
        s: "",
        l: 10,
        p: 1,
        oby: "created_at", // order_by (role_id|name|created_at)
        sort: "desc"
      },

      // Responsive
      windowWidth: window.innerWidth,
    };
  },

  methods: {
    // Create
    handleCreate() {
      this.resetForm();
      this.formDialog = {
        ...this.formDialog,
        status: "create",
        visible: true
      };
      this.$nextTick(() => {
        this.$refs["roleForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    createData() {
      this.$refs.roleForm.validate(valid => {
        if (valid) {
          this.loading = true;

          this.$store
            .dispatch("AddRole", this.roleForm)
            .then(res => {
              this.getRoles();

              this.$message.success(res.data.messages[0]);
              this.formDialog.visible = false;
              this.loading = false;
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Update
    handleUpdate(row) {
      this.roleForm = {
        name: row.name,
        access_ids: row.accesses.map(access => {
          return access.access_id;
        })
      };
      this.formDialog = {
        status: "update",
        visible: true,
        selectedId: row.role_id
      };
      this.$nextTick(() => {
        this.$refs["roleForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    updateData() {
      // console.log("this.roleForm: ", this.roleForm);
      this.$refs.roleForm.validate(valid => {
        if (valid) {
          this.loading = true;

          // add role Id
          this.roleForm.roleId = this.formDialog.selectedId;

          this.$store
            .dispatch("UpdateRole", this.roleForm)
            .then(res => {
              this.getRoles();

              this.loading = false;
              this.formDialog.visible = false;
              // this.$message.success(res.data.messages[0]);
              this.$notify({
                title: 'Success',
                message: res.data.messages[0],
                type: 'success'
              });
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Delete
    handleDelete(roles) {
      this.deleteDialog.visible = true;
      this.deleteRoleIds = roles.map(role => {
        return role.role_id
      });

      // Responsive
      this.makeDialogResponsive(dialog.sm, dialog.md30);
    },
    deleteData() {
      this.deleteDialog.visible=false;
      this.$store
        .dispatch("DeleteRole", this.deleteRoleIds)
        .then(res => {

          // refetch roles data
          this.getRoles();

          this.$message.success(res.data.messages[0]);
          this.deleteDialog.visible = false;
          this.deleteRoleIds = [];
        })
        .catch(error => {
          const errorMsg = error.response.data.messages;
          errorMsg.forEach(msg => {
            this.$message.error(msg);
          });
        });
    },

    // Form
    resetForm() {
      this.roleForm = {
        name: "",
        access_ids: []
      };
    },

    // Table
    handleSelectionChange(roles) {
      this.multipleSelection = roles;
    },

    // Pagination
    handleSizeChange(pageSize) {
      this.pagination = {
        ...this.pagination,
        pageSize
      };
      this.urlParams = {
        ...this.urlParams,
        l: pageSize
      };

      this.getRoles();
    },
    handleCurrentChange(currentPage) {
      this.pagination = {
        ...this.pagination,
        currentPage
      };
      this.urlParams = {
        ...this.urlParams,
        p: currentPage
      };
      this.getRoles();
    },

    // Get
    getRoles() {
      this.loading = true;
      this.$store.dispatch("FetchRoles", this.urlParams).then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.roles = data.data.roles;
        this.loading = false;

        // set pagination
        this.pagination = {
          ...this.pagination,
          total: data.data.total
        };
      });
    },

    // Responsive
    makeDialogResponsive(sm=dialog.sm, md=dialog.md) {
      if(this.windowWidth < 990) {
        this.formDialog = {
          ...this.formDialog,
          dialog:sm,
        }
      }else{
        this.formDialog = {
          ...this.formDialog,
          dialog:md,
        }
      }
    },
  },

  watch: {
    windowWidth(newWidth, oldWidth) {
      this.makeDialogResponsive();
    },
  },

  created() {
    this.getRoles();

    this.$store.dispatch("FetchAccesses").then(accesses => {
      this.accesses = accesses;
    });
  },

  mounted() {
    let that = this;
    this.$nextTick(function() {
      window.addEventListener('resize', function(e) {
        that.windowWidth = window.innerWidth
      });
    })
  },
};
</script>

<style scoped>

</style>



